@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    :root {
        @apply font-poppins;

        @media (max-width: 840px) {
            font-size: 14px;
        }
    }
}

@layer components {
    .btn-solid-primary {
        @apply bg-primary px-3 py-2 flex justify-center items-center text-white rounded-md select-none cursor-pointer;
    }

    .form-input-field {
        @apply px-4 py-2 w-full outline-primary outline-1 rounded-md;
    }
}

@layer utilities {
    .scrollbar-none::-webkit-scrollbar {
        display: none;
    }

    .spinner_V8m1 {
        transform-origin: center;
        animation: spinner_zKoa 2s linear infinite;
    }
    .spinner_V8m1 circle {
        stroke-linecap: round;
        animation: spinner_YpZS 1.5s ease-in-out infinite;
    }
    @keyframes spinner_zKoa {
        100% {
            transform: rotate(360deg);
        }
    }
    @keyframes spinner_YpZS {
        0% {
            stroke-dasharray: 0 150;
            stroke-dashoffset: 0;
        }
        47.5% {
            stroke-dasharray: 42 150;
            stroke-dashoffset: -16;
        }
        95%,
        100% {
            stroke-dasharray: 42 150;
            stroke-dashoffset: -59;
        }
    }
}
